/* Style the form */
#regForm {
  background-color: #ffffff;
  /* margin: 100px auto; */
  padding: 10px;
  width: 70%;
  /* min-width: 300px; */
}

#digit-number {
  display: none;
}


/* Mark input boxes that gets an error on validation: */
input.invalid {
  background-color: #ffdddd;
}

input.NaNinvalid {
  background-color: #ffdddd;
}

select.NaNinvalid {
  background-color: #ffdddd;
}

/* Hide all steps by default: */
.tab {
  display: none;
}

.main-title {
    margin: 50px;
}

.tab {
  display: none;
}

.stepper {
  display: flex;
  justify-content: center;
  margin: 15px;
}

.step {
  display: flex;
  padding: 5px;
  justify-content: center;
  align-items: center;
}

.circle {
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #4F167A;
  border: none;
  border-radius: 50%;
  display: inline-block;
  opacity: 0.5;
}

/* Mark the active step: */
.circle.active {
  opacity: 1;
  background-color: #4F167A;
}
/* Mark the steps that are finished and valid: */
.circle.finish {
  background-color: #4F167A;
}

input[type=radio] {
  padding: 5px;
  margin: 5px;
	accent-color: #4F167A;
}

.hr-line {
  border: 1px solid #e5e5e5;
  width: 55%;
  margin: auto;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;;
}

.investments {
  display: flex;
  flex-direction: column;
  margin: 0px 375px;
  justify-content: flex-start;
  align-items: flex-start;
  height: 60vh;
}

.investment-title {
  /* margin: 10px 15px 0px 15px; */
  text-align: left;
  width: 100%;
}

.fund-title {
  margin: 40px 0px 0px 0px;
  text-align: center;
  width: 100%;
}

.box-grey {
  margin: 25px 30px;
  background-color: #ebebeb;
  width: 625px;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;;
}

.space-between {
  justify-content: space-between !important;
}

.num-of-shares {
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 20px;
}

.own {
  height: 170px;
}

.own-btns {
  display: flex;
  justify-content: center;
  margin: 10px 0 0 0 ;
}

.btn-invest {
  margin: 10px !important;
  background-color: #fff;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;;
  height: 120px;
  width: 100px;
  text-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;;
  color: #717070;
  border-radius: 5px;
}

.fa-circle-check {
  color: #4F167A;
  box-shadow: none;
  text-shadow: none;
  /* background-color: #fff; */
  font-size: 40px;
  margin: 35px 10px 10px 10px;
}

.active {
  background-color: #CEA3EE;
  border: 2px solid #4F167A;
  color: #fff;
  box-shadow: none;
  text-shadow: none;
}

.btn-invest:hover, .btn-invest:active {
  background-color: #CEA3EE;
  border: 2px solid #4F167A;
  color: #fff;
  box-shadow: none;
  text-shadow: none;
}

.btn-bar {
  width: 200px;
  height: 50px;
  background-color: #4F167A;
  color: #fff;
  border: none;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;;
  text-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;;
  margin: 0 10px 25px 0;
}

.btn-bar:hover, .btn-bar-sig:hover {
  background-color: #CEA3EE;
}

.btn-bar-sig {
  width: 100px;
  height: 35px;
  background-color: #4F167A;
  color: #fff;
  border: none;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;;
